const defaultComparator = (a, b) => {
  if (a === b) {
    return true;
  }

  return false;
}

/**
 * 
 */
export class LinkedListNode {
  public value;

  public next: LinkedListNode;

  constructor(value, next: LinkedListNode = null) {
    this.value = value;
    this.next = next;
  }

  toString(callback?: any) {
    return callback ? callback(this.value) : `${this.value}`;
  }
}

export default class LinkedList {
  public head: LinkedListNode;

  public tail: LinkedListNode;

  public compare: Function;

  constructor(comparator = defaultComparator) {
    this.head = null;
    this.tail = null;
    this.compare = comparator;
  }

  /**
   * 
   * @param value
   */
  prepend(value) {
    // 
    const newNode = new LinkedListNode(value, this.head);
    this.head = newNode;

    if (!this.tail) {
      this.tail = newNode;
    }

    return this;
  }

  /**
   * 
   * @param value
   */
  append(value) {
    const newNode = new LinkedListNode(value);

    // 
    if (!this.head) {
      this.head = newNode;
      this.tail = newNode;

      return this;
    }

    // 
    this.tail.next = newNode;
    this.tail = newNode;

    return this;
  }

  /**
   * 
   * @param value 
   */
  delete(value): LinkedListNode {
    if (!this.head) {
      return null;
    }

    let deleteNode = null;

    // next
    while (this.head && this.compare(this.head.value, value)) {
      deleteNode = this.head;
      this.head = this.head.next;
    }

    let currentNode = this.head;

    if (currentNode !== null) {
      // next
      while (currentNode.next) {
        if (this.compare(currentNode.next.value, value)) {
          deleteNode = currentNode.next;
          currentNode.next = currentNode.next.next;
        } else {
          currentNode = currentNode.next;
        }
      }
    }

    // 
    if (this.compare(this.tail.value, value)) {
      this.tail = currentNode;
    }

    return deleteNode;
  }

  /**
   * 
   * @param param0
   */
  find({ value = undefined, callback = undefined }): LinkedListNode {
    if (!this.head) {
      return null;
    }

    let currentNode = this.head;

    while (currentNode) {
      //  callback callback 
      if (callback && callback(currentNode.value)) {
        return currentNode;
      }

      //  value value 
      if (value !== undefined && this.compare(currentNode.value, value)) {
        return currentNode;
      }

      currentNode = currentNode.next;
    }

    return null;
  }

  /**
   * 
   */
  deleteTail() {
    const deletedTail = this.tail;

    if (this.head === this.tail) {
      // 
      this.head = null;
      this.tail = null;
      return deletedTail;
    }

    let currentNode = this.head;
    while (currentNode.next) {
      if (!currentNode.next.next) {
        currentNode.next = null;
      } else {
        currentNode = currentNode.next;
      }
    }

    this.tail = currentNode;

    return deletedTail;
  }

  /**
   * 
   */
  deleteHead() {
    if (!this.head) {
      return null;
    }

    const deletedHead = this.head;

    if (this.head.next) {
      this.head = this.head.next;
    } else {
      this.head = null;
      this.tail = null;
    }

    return deletedHead;
  }

  /**
   * 
   * @param values 
   */
  fromArray(values) {
    values.forEach((value) => this.append(value));
    return this;
  }

  /**
   * 
   */
  toArray() {
    const nodes = [];

    let currentNode = this.head;

    while (currentNode) {
      nodes.push(currentNode);
      currentNode = currentNode.next;
    }

    return nodes;
  }

  /**
   * 
   */
  reverse() {
    let currentNode = this.head;
    let prevNode = null;
    let nextNode = null;
    while (currentNode) {
      // 
      nextNode = currentNode.next;

      // 
      currentNode.next = prevNode;

      //  prevNode  currentNode 
      prevNode = currentNode;
      currentNode = nextNode;
    }

    this.tail = this.head;
    this.head = prevNode;
  }

  toString(callback = undefined) {
    return this.toArray()
      .map((node) => node.toString(callback))
      .toString();
  }
}
